@import "../../assets/style/index.scss";

.description-container {
  // width: 100%;
  padding-bottom: 50px;

  .description-box {
    // width: 50%;
    display: flex;
    background: $color-ghost-white;
    justify-content: space-between;
    padding: 120px 103px;
    
    .title {
      width: 60%;
      display: flex;
      flex-direction: column;
      gap: 32px;

      h2 {
        color: $color-black;
        font-family: $family-manrope;
        font-size: 48px;
        font-style: normal;
        font-weight: 600;
        line-height: 120%; /* 57.6px */
      }

      p {
        color: $color-dim-gray;
        font-family: $family-manrope;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
      }
    }

    .description-btn-box {
      // width: 40%;
      width: 200px;
      height: 200px;
      background-image: url("../../assets/img/description/register-btn.png");
      // border-radius: 50%;
      // border: 2px solid #000;
      // position: relative;

      align-self: center;

      .text {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: justify;

        span {
          display: inline-block;
          position: absolute;
          font-size: 14px; /* Adjust as needed */
          transform-origin: 50% 100%;

          color: $color-black;
          font-family: $family-manrope;
          font-size: 18px;
          font-style: normal;
          font-weight: 500;
          line-height: normal;
          text-transform: uppercase;

          &:nth-child(1) {
            display: flex;
            width: 11.937px;
            height: 24.868px;
            transform: rotate(1.769deg);
            flex-direction: column;
            justify-content: center;
            flex-shrink: 0;
          }
        }
      }
    }
  }
}